import { Button, Nav, NavItem } from "reactstrap";
import { Link, useLocation } from "react-router-dom";
import user1 from "../assets/images/logos/logoPrincipal.jpg";
import probg from "../assets/images/logos/Afuera.png";

const navigation = [
  {
    title: "INICIO",
    href: "/starter",
    icon: "bi bi-speedometer2",
  },
  {
    title: "PRESENTACION",
    href: "/presentacion",
    icon: "bi bi-bell",
  },
  {
    title: "RESEÃ‘A HISTORICA",
    href: "/historia",
    icon: "bi bi-patch-check",
  },
  {
    title: "MISION",
    href: "/mision",
    icon: "bi bi-hdd-stack",
  },
  {
    title: "VISION",
    href: "/vision",
    icon: "bi bi-card-text",
  },
  {
    title: "VALORES",
    href: "/valores",
    icon: "bi bi-columns",
  },
  {
    title: "OBJETIVOS",
    href: "/objetivo",
    icon: "bi bi-layout-split",
  },
  {
    title: "ESTRUCTURA ORGANICA",
    href: "/estructura",
    icon: "bi bi-textarea-resize",
  },
  {
    title: "Breadcrumbs",
    href: "/breadcrumbs",
    icon: "bi bi-link",
  },
  {
    title: "About",
    href: "/about",
    icon: "bi bi-people",
  },
];

const Sidebar = () => {
  const showMobilemenu = () => {
    document.getElementById("sidebarArea").classList.toggle("showSidebar");
  };
  let location = useLocation();

  return (
    <div>
      <div className="d-flex align-items-center"></div>
      <div
        className="profilebg"
        style={{ background: `url(${probg}) no-repeat` }}
      >
        <div className="p-3 d-flex">
          <div></div>
          <img src={user1} alt="user" width="50" className="rounded-circle" />
          <Button
            color="white"
            className="ms-auto text-white d-lg-none"
            onClick={() => showMobilemenu()}
          >
            <i className="bi bi-x"></i>
          </Button>
        </div>
        <div className="bg-dark text-white p-2 opacity-75">FCEFA</div>
      </div>
      <div className="p-3 mt-2">
        <Nav vertical className="sidebarNav">
          {navigation.map((navi, index) => (
            <NavItem key={index} className="sidenav-bg">
              <Link
                to={navi.href}
                className={
                  location.pathname === navi.href
                    ? "active nav-link py-3"
                    : "nav-link text-secondary py-3"
                }
              >
                <i className={navi.icon}></i>
                <span className="ms-3 d-inline-block">{navi.title}</span>
              </Link>
            </NavItem>
          ))}
          
        </Nav>
      </div>
    </div>
  );
};

export default Sidebar;
